.App {
  text-align: center;
  height: 100vh;
  overflow: hidden;
}



.main {
  background-color: #ececec;
  color: #1d1c1a;
  font-family: 'Helvetica Neue', sans-serif;
  width: 100vw;
  font-weight: 900;
  font-size: 20px;
  
  height: 100vh;
}
.main p {
  margin:0;
  position: absolute;
  top:30%;
  left:30%;
}
.gif {
  background-image:url('/public/giphy.gif');
  background-repeat:repeat;
  width: 90vw;
  height: 90vh;
  margin: auto;
  position: relative;
}
.gif p {
  position: absolute;
  top:50%;
  left:50%;
  background-color: white;
  padding: 12px 20px 12px 20px;
  transform: translate(-50%,-50%);
  transition: all .3s ease-in-out;
}
.gif p:hover {
  background: black;
  color:white;
  cursor:pointer;
  transition: all .3s ease-in-out;
  padding: 5px 18px 5px 18px;

}